import { ResponsiveContext } from "grommet";
import {
  PageHeader,
  Anchor,
  Button,
  Main,
  Heading,
  Paragraph,
  Card,
  Text,
  CardHeader,
  CardBody,
  CardFooter,
  Box,
  Video,
  Accordion,
  AccordionPanel
} from "grommet/components";

import { Link, useParams } from "react-router-dom";
import HomeSites from "./Home_Sites";
import Sustainability from "./Sustainability";
import SwitchLanguage from "./SwitchLanguage";
let hometext = require("../text/homepage.json");

export default function HomePage() {
  let params = useParams();

  return (
    <Main>
      <Box overflow={{ horizontal: "hidden" }}>
        <PageHeader
          title="Welcome!"
          subtitle="Scotland's Summer of Scent."
          parent={<Anchor label="Scottish Lavender Oils" />}
        />

        <Accordion>
          <AccordionPanel label="Welcome! (Text)">
            <Box pad="medium" background="light-2">
              {" "}
              <p>{hometext[params.language]["welcome"]}</p>
              <Text>
                To quickly know what your speaking rate is, write a 650-word
                essay and then get out a stopwatch. Time how long it takes you
                to speak all of the words in the essay. Once you’ve finished,
                divide the word count (650 words) by the number of minutes in
                decimals (30 seconds = 0.5 minutes) and you’ll get an estimate
                of your words per minute speaking rate. For example, if it takes
                you 4.5 minutes to speak 650 words, you would do 650 divided by
                4.5 and get 144 words per minute.
              </Text>
            </Box>
          </AccordionPanel>
          <AccordionPanel label="Welcome! (Video)">
            <Box pad="medium" background="light-2">
              <Video controls="over" fit="cover">
                <source
                  key="video"
                  src="https://cdn.shopify.com/s/files/1/0495/5216/2971/files/Classic_Video_Final.mp4"
                />
                <track
                  key="cc"
                  label="English"
                  kind="subtitles"
                  srcLang="en"
                  src="/assets/small-en.vtt"
                  default
                />
              </Video>
            </Box>
          </AccordionPanel>
        </Accordion>

        <br />
        <Card width="100%" background="dark-1">
          <CardHeader pad="medium">Tour Stops</CardHeader>
          <CardBody pad="medium">
            <HomeSites />
          </CardBody>
        </Card>
        <br />
        <Sustainability />
        <SwitchLanguage />
      </Box>
    </Main>
  );
}
