import { Button } from "grommet";
import { Link, useParams } from "react-router-dom";
let hometext = require("../text/homepage.json");

export default function HomeSites() {
  let params = useParams();

  return (
    <div>
      <Link to={`/${params.language}/site/lavender`}>
        <Button
          margin="xxsmall"
          primary
          label={`1 - ${hometext[params.language].lavender}`}
        />
      </Link>
      <br />
      <Link to={`/${params.language}/site/lab`}>
        <Button
          margin="xxsmall"
          primary
          label={`2 - ${hometext[params.language].studio}`}
        />
      </Link>
      <br />
      <Link to={`/${params.language}/site/wild`}>
        <Button
          margin="xxsmall"
          primary
          label={`3 - ${hometext[params.language].wildflowers}`}
        />
      </Link>
      <br />
      <Link to={`/${params.language}/site/dist`}>
        <Button
          margin="xxsmall"
          primary
          label={`4 - ${hometext[params.language].distillery}`}
        />
      </Link>
      <br />
      <Link to={`/${params.language}/site/dome`}>
        <Button
          margin="xxsmall"
          primary
          label={`5 - ${hometext[params.language].dome}`}
        />
      </Link>
      <br />
      <Link to={`/${params.language}/site/distance`}>
        <Button
          margin="xxsmall"
          primary
          label={`6 - ${hometext[params.language].distance}`}
        />
      </Link>
    </div>
  );
}
