import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import ReactPlayer from "react-player/vimeo";

import {
  Accordion,
  Card,
  CardHeader,
  CardBody,
  Button,
  AccordionPanel,
  Text,
  Box,
  Video,
  Anchor,
  PageHeader,
  Main,
  Carousel,
  Image,
  CardFooter,
  Heading
} from "grommet";
import Scents from "./Scents";

let text = require("../text/sitetext.json");

export default function PageTemplate(props) {
  let params = useParams();
  let textuse = text[params.language][params.pagename];
  return (
    <Main>
      <Box background="dark-3">
        <Text alignSelf="center">
          <Link to={`/${params.language}/home`}>
            <Button secondary label="Return Home" margin="xsmall" />
          </Link>
        </Text>
      </Box>

      <PageHeader
        title={textuse.title}
        subtitle={textuse.subtitle}
        parent={<Anchor label="Purple Haze" />}
      />

      <Accordion>
        <AccordionPanel label="Guide (Text)">
          <Box pad="medium" background="light-2">
            <Text>
              {textuse.text.map((para) => (
                <p>{para}</p>
              ))}
            </Text>
          </Box>
        </AccordionPanel>
        <AccordionPanel label="Guide (Video)">
          <Box pad="medium" background="light-2">
            <Video controls="over" fit="cover">
              <source
                key="video"
                src="https://cdn.shopify.com/s/files/1/0495/5216/2971/files/Classic_Video_Final.mp4"
              />
              <track
                key="cc"
                label="English"
                kind="subtitles"
                srcLang="en"
                src="/assets/small-en.vtt"
                default
              />
            </Video>
          </Box>
        </AccordionPanel>
      </Accordion>
      <br />

      <Box>
        <PageHeader
          alignSelf="center"
          title="Photos"
          subtitle="Use #ScottishLavenderOils to be featured!"
          parent={<Anchor label="Join in!" />}
        />
        <Carousel fill>
          <Image
            fit="contain"
            src="//v2.grommet.io/assets/Wilderpeople_Ricky.jpg"
          />
          <Image fit="contain" src="//v2.grommet.io/assets/IMG_4245.jpg" />
          <Image fit="contain" src="//v2.grommet.io/assets/IMG_4210.jpg" />
        </Carousel>
      </Box>

      <br />
      <PageHeader
        title="Scent Tour"
        subtitle="You can find the oils at each of the tour checkpoints."
        parent={<Anchor label="Follow the Scent Trail" />}
      />
      <Scents />
    </Main>
  );
}
