import { useParams, Link, useLocation } from "react-router-dom";
import {
  PageHeader,
  Anchor,
  Button,
  Main,
  Heading,
  Paragraph,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Box
} from "grommet/components";

export default function SwitchLanguage() {
  let location = useLocation();
  let pathname = location.pathname.split("/").splice(2, 5).join("/");

  return (
    <div>
      <br />
      <Card width="100%" background="dark-1">
        <CardHeader pad="medium">Switch Language</CardHeader>
        <CardBody pad="medium">
          <Link to={`/en/${pathname}`}>
            <Button margin="xxsmall" primary label="English" />
          </Link>
          <Link to={`/pl/${pathname}`}>
            <Button margin="xxsmall" primary label="Polish" />
          </Link>
        </CardBody>
      </Card>
    </div>
  );
}
