import {
  Card,
  CardHeader,
  CardBody,
  Button,
  Box,
  NameValuePair,
  NameValueList,
  Text,
  Clock,
  List
} from "grommet";

export default function Sustainability() {
  return (
    <Card width="100%" background="light-2">
      <CardHeader pad="medium">Scottish Lavender Oils in Numbers</CardHeader>
      <CardBody pad="medium">
        <Box width="medium">
          <List
            primaryKey="name"
            secondaryKey="number"
            data={[
              { name: "New trees", number: 4782 },
              { name: "Re-wilded land", number: "40 ac" },
              { name: "Plant varieties", number: 32 },
              { name: "Avg. daily hares", number: 12 }
            ]}
          />
        </Box>
      </CardBody>
    </Card>
  );
}
